body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-size: 62.5%; }

body {
  font-size: 1.5rem;
  margin: 0px;
  padding: 0px;
  font-family: Helvetica, Arial, sans-serif;
  background: #d2d2d2; }

.container {
  max-width: 100rem;
  margin: 0 auto;
  padding: 0 0; }

.map-container {
  width: 100rem !important; }

.map {
  max-width: 100rem !important;
  max-height: 50rem !important; }

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 3rem; }

.form-select {
  text-align: center;
  margin-bottom: 1rem; }

.textbox {
  color: #20ac04b6;
  font-weight: 800;
  border: none;
  padding: .5rem 2rem;
  -webkit-flex-grow: 7;
          flex-grow: 7;
  border-bottom: 3px solid #20ac04b6;
  margin-bottom: 1rem; }

.form-title {
  text-align: center;
  color: #36a200;
  font-weight: 900; }

.form-label {
  color: #36a200;
  font-weight: 700;
  text-align: center; }

.textbox:focus {
  outline: 0; }

.button:focus {
  outline: 0; }

.button {
  outline: none;
  background-color: #20ac04b6;
  color: white;
  border: none;
  padding: .5rem 2rem;
  border-bottom: rgba(0, 0, 0, 0.713725);
  -webkit-flex-grow: 3;
          flex-grow: 3; }

.btn {
  padding: 1rem 2rem !important; }

.button:active {
  -webkit-transform: scale(1.1);
          transform: scale(1.1); }

.nav {
  margin: 0 0 0 0;
  padding: 1rem 0 1rem 0;
  background-color: #319300;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 2rem; }

.nav-link:hover {
  color: rgba(238, 254, 217, 0.945); }

.nav-link:visited {
  color: rgba(238, 254, 217, 0.945); }

.nav-link {
  color: #8cf486f1 !important;
  text-decoration: none;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.6rem; }

.nav-link--active {
  font-weight: 900;
  color: #ffffff !important;
  text-decoration: none; }

.header {
  text-align: center;
  margin: 0;
  color: white;
  padding: 1rem 2rem;
  background-color: #36a200; }

.header-title {
  margin: 0;
  font-size: 3.52rem; }

.header-subtitle {
  margin: 0;
  font-size: 2rem; }

.list {
  height: 50rem;
  overflow: hidden; }

.cardlist {
  overflow: auto;
  height: 50rem; }

.card {
  width: 100% !important;
  margin-bottom: 1rem !important; }

.information {
  background-color: #ffffff;
  border-radius: 1rem;
  padding: 2rem 1rem;
  text-align: center; }

.info-title {
  font-size: 3rem;
  color: #36a200;
  font-weight: 500; }

.info-subtitle {
  font-size: 2rem;
  color: #36a200;
  font-style: italic; }

.info-img {
  width: 20rem;
  height: 20rem;
  margin: 2rem 0rem; }

.info-link {
  margin-top: 2rem;
  color: #36a200; }

.info-link:hover {
  color: #36a200; }

